<template>
    <v-app>
        
    </v-app>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase'

export default {
    props: {
        ResultData: Object
    },
    data() {
        return {
            Database: null,
            K: 32
        }
    },
    watch: {
        ResultData(val) {
            this.UpdateResult(val.cardWon, val.cardLost)
        },
    },
    mounted() {
        this.InitializeFirebase()
    },
    methods: {
        InitializeFirebase() {
            var firebaseConfig = {
                apiKey: "AIzaSyAQPj00DqC81olX83FCfNMDIEmHiWsUlz4",
                authDomain: "cardvalueengine.firebaseapp.com",
                databaseURL: "https://cardvalueengine-default-rtdb.europe-west1.firebasedatabase.app",
                projectId: "cardvalueengine",
                storageBucket: "cardvalueengine.appspot.com",
                messagingSenderId: "365300806166",
                appId: "1:365300806166:web:3367c1df649e0a1cd63b37",
                measurementId: "G-1TZ6Y8S3ZG"
            };

            firebase.initializeApp(firebaseConfig)

            this.Database = firebase.database()
        },
        UpdateResult(cardWon, cardLost) {
            var resultCalculations = firebase.database().ref('planeswalkers/resultsCalculations')
            resultCalculations.transaction(function(current) {
                if (current === null) {
                    current = {
                        results: 1
                    }
                }
                else {
                    current.results = current.results + 1
                }
                return current
            })

            var winCardRef = firebase.database().ref('planeswalkers/cardratings/' + cardWon.name);
            var loserCardRef = firebase.database().ref('planeswalkers/cardratings/' + cardLost.name)

            var winCardData = null
            var loserCardData = null

            winCardRef.transaction(function(currentWinData) {
                winCardData = currentWinData
                
                if (winCardData === null) {
                    console.log("W-" + cardWon.name + " had not been created.")
                    winCardData = {
                        card: cardWon.name,
                        cardsDefeated: [{
                            name: cardLost.name,
                            amount: 1
                        }],
                        cardData: cardWon,
                        rating: 1500.0
                    }
                } else {
                    console.log("W-" + winCardData.card + " was found.")
                    var dataFound = false
                    
                    if (!winCardData.cardsDefeated) {
                        console.log("W-" + winCardData.card + " didn't have cardsDefeated.")
                        winCardData.cardsDefeated = []
                    }

                    winCardData.cardsDefeated.find(function (x) {
                        console.log("W-" + winCardData.card + " checking for def(x|cardLost.name): " + x.name + " ?? " + cardLost.name)
                        if (x.name == cardLost.name) {
                            x.amount++
                            dataFound = true
                            return
                        }

                        return false
                    })

                    if (!dataFound) {
                        console.log("W-" + winCardData.card + " defeated " + cardLost.name + " for the first time.")
                        winCardData.cardsDefeated.push({name: cardLost.name, amount: 1})
                    }
                    console.error(winCardData.cardsDefeated)
                    
                }

                loserCardRef.transaction(function(currentLoserData) {
                    loserCardData = currentLoserData
                    
                    if (loserCardData === null) { // Didn't have data, made new
                        console.log("W-" + cardLost.name + " had not been created.")
                        loserCardData = { 
                            card: cardLost.name, 
                            cardsDefeated: [],
                            cardData: cardLost,
                            rating: 1500.0
                        }
                    }
                    
                    // Calculate new ratings.
                    // For elo calculations, used 
                    // https://dev.to/bariscanyilmaz/elo-rating-system-1o2l

                    var winnerProbability = 1.0 / (1 + 1.0 *
                            (Math.pow(10, 1.0 *
                                (loserCardData.rating - winCardData.rating) / 400)))

                    var loserProbability = (1 - winnerProbability)

                    var K = 32 // K value for rating calculations

                    winCardData.rating = winCardData.rating + 
                            K * (1 - winnerProbability)

                    loserCardData.rating = loserCardData.rating + 
                            K * (0 - loserProbability)
                    
                    return loserCardData
                })

                currentWinData = winCardData
                console.error(currentWinData.cardsDefeated)
                console.error(winCardData.cardsDefeated)

                return winCardData
            })
        },
        GetWinProbability(winnerRating, loserRating) {
            return 1.0 / (1 + 1.0 *
                Math.pow(10, 1.0 *
                    (loserRating - winnerRating) / 400))
        },
    }

}
</script>

